import { Component, AfterViewInit, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { ValidationErrors } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
//
import { forkJoin, Observable } from 'rxjs';
import { MatDialog, MatDialogRef, MatSnackBar } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';
//
import { Face, MainImage, State, Status } from '../../../../../ui/modules/images-card/models/face';
import { CanDeactivateMixin } from '../../../../../ui/helpers/component-can-deactivate';
import { ConfirmDialogComponent } from '../../../../../ui/modules/confirm-dialog/components/confirm-dialog/confirm-dialog.component';
import { Mixin } from '../../../../../ui/helpers/mixin-decorator';
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
import { ToastrService } from '../../../../../error-handling/services/toastr.service';
import { Shop, ShopImage } from '../../models/shops';
import { ShopsService } from '../../services/shops.service';
import { ShopsImgesService } from '../../services/shops-images.service';
import { ImagesService } from '../../../../../ui/modules/images-card/services/images.service';
//import { setTranslations } from '@c/ngx-translate';

import { Brand } from '../../../ms-brands/models/brand';
import { Category } from '../../../ms-categories/models/category';
import { Collection } from '../../../ms-collections/models/collection';
import { Release } from '../../../ms-releases/models/releases';



const errorKey = 'Error';

const updatedShopMessageKey = 'Updated';

@Component({
  selector: 'edit-shop',
  templateUrl: './edit-shop.component.html',
  styleUrls: ['./edit-shop.component.scss']
})

@Mixin([CanDeactivateMixin])
export class EditShopComponent implements AfterViewInit, CanDeactivateMixin {

  brands: Array<Brand>;

  categories: Array<Category>;

  collections: Array<Collection>;

  dealId: string;

  data: Shop;

  faceList: Array<Face> = [];

  releases: Array<Release>;

  validationErrors: ValidationErrors;

  shops: Array<Shop>;

  // Begin Mixin code of the CanDeactivate class
  unsavedChanges = false;

  cancelBtnKey = 'No';

  okBtnKey = 'Yes';

  saveTitleKey = 'Discard Title';

  saveMessageKey = 'Discard Message';

  modalRef: MatDialogRef<ConfirmDialogComponent>;

  shopId: string;

  returnUrl: string;

  canDeactivate: () => Observable<boolean> | boolean;

  dataChanged: () => void;

  //@Output() close = new EventEmitter();

  constructor(
    public activatedRoute: ActivatedRoute,
    public dialog: MatDialog,
    public shopsService: ShopsService,
    private errorHandlingService: ErrorHandlingService,
    public imagesService: ImagesService,
    public shopsImgesService: ShopsImgesService,
    public router: Router,
    public snackBar: MatSnackBar,
    private translate: TranslateService,
    private toastr: ToastrService) {
    //setTranslations(this.translate, TRANSLATIONS);
  }


  ngOnInit() {
    this.shopId = this.activatedRoute.snapshot.data.shopId;
    this.brands = this.activatedRoute.snapshot.data.brands;
    this.categories = this.activatedRoute.snapshot.data.categories;
    this.collections = this.activatedRoute.snapshot.data.collections;
    this.faceList = this.activatedRoute.snapshot.data.shopAllImages;
    this.shops = this.activatedRoute.snapshot.data.shops;
    this.releases = this.activatedRoute.snapshot.data.releases;

    this.dealId = this.activatedRoute.snapshot.queryParams.dealId;
    this.returnUrl = this.activatedRoute.snapshot.queryParams['returnUrl'];
    /*this.shopsImgesService.getShopAllImages(this.shopId).subscribe(response => {
      this.faceList = response.data;
    },
      (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error)
    )*/
  }
  ngAfterViewInit() {
    this.getShop();
  }

  getShop() {
    this.shopsService.getShop(this.shopId).subscribe(response => {
      this.data = response.data;
      if (this.data.mainImage) {
        this.faceList.forEach((image) => {
          image.fileName = "";
          if (image.imgUrl === this.data.mainImage) {
            image.mainImage = true;
          }
        });
      }
      this.data.faces = this.faceList;
    },
      (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error)
    )
  }

  submit(data: Shop) {
    delete data.updatedAt;
    delete data.createdAt;
    this.editShop(data);
  }

  cancel() {
    this.close();
  }

  close() {
    if (this.returnUrl && this.returnUrl.length > 0) {
      this.router.navigateByUrl(this.returnUrl);
    } else {
      this.router.navigate(this.activatedRoute.snapshot.data.closeRouteCommand, { relativeTo: this.activatedRoute });
    }
  }

  updateShop(data: Shop) {
    this.shopsService.putShop(data).subscribe(response => {
      this.unsavedChanges = false;
      this.close();
      this.toastr.success(updatedShopMessageKey);
    },
      (error: HandledError) => {
        this.errorHandlingService.handleUiError(errorKey, error);
        this.validationErrors = error.formErrors;
      });
  }

  editShop(shopData: Shop) {
    let mainImageFlag: boolean = false;
    if (shopData !== undefined && shopData !== null) {
      // Saving the images of the case before saving the case
      shopData.images = [];
      const imagesObservables = new Array<Observable<any>>();
      const addedFaces = new Array<Face>();
      // Uploading the images to command and control server
      for (const position in shopData.faces) {
        const face = shopData.faces[position];
        if (face.state === State.New) {
          face.status = Status.Uploading;
          if (face.mainImage === true) {
            mainImageFlag = true;
            this.imagesService.postImage(face.file).subscribe(response => {
              let image = new ShopImage;
              image.imgUrl = response.data.url;
              //shopData.images = [...shopData.images, image];
              let mainImage: MainImage = {
                mainImage: image.imgUrl
              }
              this.shopsImgesService.patchShopMainImage(this.shopId, mainImage).subscribe(response => {
                console.log("new principal");
              },
                (error: HandledError) => {
                  this.errorHandlingService.handleUiError(errorKey, error);
                  this.validationErrors = error.formErrors;
                });

              this.shopsImgesService.postShopImage(this.shopId, image).subscribe(response => {
                console.log("salvando imagenes");
              },
                (error: HandledError) => {
                  this.errorHandlingService.handleUiError(errorKey, error);
                  this.validationErrors = error.formErrors;
                });
            },
              (error: HandledError) => {
                this.errorHandlingService.handleUiError(errorKey, error);
                this.validationErrors = error.formErrors;
              });
          } else {
            const subscription$ = this.imagesService.postImage(face.file);
            imagesObservables.push(subscription$);
          }
          addedFaces.push(face);
        } else if (face.mainImage === true) {
          mainImageFlag = true;
          let mainImage: MainImage = {
            mainImage: face.imgUrl
          }
          shopData.mainImage = face.imgUrl;
          /*this.shopsImgesService.patchShopMainImage(this.shopId, mainImage).subscribe(response => {
            console.log("old principal");
          });*/
        }
      }
      if (imagesObservables.length > 0) {

        forkJoin(imagesObservables).subscribe(responses => {
          for (const item in responses) {
            let image = new ShopImage;
            image.imgUrl = responses[item].data.url;
            shopData.images = [...shopData.images, image]
          }
          this.shopsImgesService.postShopImageAll(shopData.id, shopData.images).subscribe(response => {
            console.log("salvando imagenes");
          },
            (error: HandledError) => {
              this.errorHandlingService.handleUiError(errorKey, error);
              this.validationErrors = error.formErrors;
            });
          if (!mainImageFlag) {
            shopData.mainImage = "";
          }
          this.shopsService.putShop(shopData).subscribe(response => {
            this.unsavedChanges = false;
            this.data = response;
            this.translate.get(updatedShopMessageKey).subscribe(value => this.toastr.success(value));
            this.close();
          },
            (error: HandledError) => {
              this.errorHandlingService.handleUiError(errorKey, error);
              this.validationErrors = error.formErrors;
            });
        },
          (error: HandledError) => {
            this.errorHandlingService.handleUiError(errorKey, error);
            this.validationErrors = error.formErrors;
          });
      } else {
        if (!mainImageFlag) {
          shopData.mainImage = "";
        }
        this.shopsService.putShop(shopData).subscribe(response => {
          this.unsavedChanges = false;
          this.data = response;
          this.translate.get(updatedShopMessageKey).subscribe(value => this.toastr.success(value));
          this.close();
        },
          (error: HandledError) => {
            this.errorHandlingService.handleUiError(errorKey, error);
            this.validationErrors = error.formErrors;
          });
      }
    }
  }

}